@import url('https://fonts.googleapis.com/css2?family=Merriweather:wght@300;400;700;900&family=Poppins:wght@100;200;300;400;600;700&display=swap');

:root{
	/** fonts */
	--ft-1: 'Merriweather', serif;
	--ft-2: 'Chivo Mono', monospace;
	--ft-3: 'Poppins', sans-serif;

	/** colors */
	--blue-400: #040934;
	--blue-300: #002987;
	--blue-200: #1947fd;
	--blue-100: #00ddee;
	--light: #fff;
	--green: #0cd42d;
	--red: #d90000;
	--border: #edf5fb;
	--text-200: #040b20;
	--text-100: #9ea8b1;
	--orange: #fb6402;


	/** gradients **/
	--grad-pink: linear-gradient(135deg, #fe8dc6, #fed1c7);
	--grad-purple: linear-gradient(135deg, #7f00ff, #e100ff);
	--grad-yellow: linear-gradient(135deg, #fbb040, #f9ed32);
	--grad-blue: linear-gradient(135deg, #00a1ff, #00ff8f);
	--text-grad: linear-gradient(90deg, #00e4ff, #1b4ce7);

	/** default */
	font-size: 62.5%;

}

*{
	margin: 0;
	padding: 0;
	box-sizing: border-box;
}

body {
	font-family: var(--ft-1);
	color: var(--light);
	background: var(--blue-400);
	font-size: 1rem;
	overflow-x: hidden;
	width: 100%;
	height: 100vh;
	min-height: 92rem;
	overflow-y: auto;
  	-webkit-font-smoothing: antialiased;
  	-moz-osx-font-smoothing: grayscale;
}

:before,
:after{
	content: '';
	position: absolute;
}

a, span{
	display: inline-block;
}

a{
	text-decoration: none;
}

img{
	width: 100%;
	object-fit: cover;
}

ul{
	list-style: none;
}

h1, h2, h3, h4{
	text-transform: capitalize;
}

button{
	border: none;
	outline: none;
	cursor: pointer;
}

.flex{
	display: flex;
}

.container{
	width: 108rem;
	height: auto;
	position: relative;
	margin: 0 auto;
}

.t-grad{
	width: fit-content;
	height: fit-content;
	color: transparent;
	background: var(--text-grad);
	-webkit-background-clip: text;
	-moz-background-clip: text;
	background-clip: text;
}