.profile{
      position: absolute;
      width: 100%;
      height: 100%;
      toP: 0;
      left: 0;
      background: var(--blue-300);
      z-index: -1;
}

/**** USER PROFILE ****/

.user_profile{
      width: 100%;
      height: 30rem;
      background: var(--blue-400);
}

.user_profile .container{
      height: 100%;
      position: relative;
}

.user_profile__content{
      height: 100%;
      align-items: center;
      gap: 4rem;
}

.user_avatar{
      width: 15rem;
      height: 15rem;
      background: rgba(0,0,0,.3);
      overflow: hidden;
      border-radius: 50%;
      box-shadow: 0 0 0 1rem rgba(255,255,255,.1), 0 0 0 2rem rgba(255,255,255,.1), 0 0 0 3rem rgba(255,255,255,.1);
}

.user_data p{
      font-family: var(--ft-2);
      font-size: 1.8rem;
}

.user_data h2{
      font-size: 4rem;
}

.user_data > span{
      font-size: 2.5rem;
      color: var(--blue-100);
}

.navbar{
      position: relative;
      height: 6rem;
      align-items: center;
      padding-left: 20rem;
      gap: 3rem;
      transform: translateY(-3rem);
}

.btn_grad{
      min-width: 15rem;
      padding: 0 3rem;
      display: flex;
      align-items: center;
      justify-content: center;
      gap: 1rem;
      height: 6rem;
      border-radius: 999px;
      position: relative;
      color: var(--light);
      text-transform: uppercase;
      font-family: var(--ft-2);
      font-size: 2rem;
      transition: .2s;
}

.btn_grad:hover{
      color: rgba(0,0,0,.5);
}

.btn_grad:before,
.btn_grad:after{
      width: 100%;
      height: 100%;
      background: inherit;
      border-radius: 999px;
      top: 0;
      left: 0;
      z-index: -1;
}

.btn_grad:after{
      filter: blur(20px);
}

.btn_grad.yellow{
      background: var(--grad-yellow);
}

.btn_grad.purple{
      background: var(--grad-purple);
}

.btn_grad.blue{
      background: var(--grad-blue);
}

.btn_grad *{
      font-size: 2.6rem;
}

.btn_grad span{
      font-size: 1.8rem;
      font-weight: 500;
}

/*** CONTENT ***/

.content_page{
      margin-top: 10rem;
}

.container.page{
      flex-direction: column;
      align-items: center;
}

.title_section h1{
      font-size: 5rem;
}

.slide_content{
      margin-top: 3rem;
      width: 80rem;
}

/***** SLIDE CARD ****/

.card_plan{
      min-width: 26rem;
      height: 34rem;
      background: var(--light);
      border-radius: 1rem;
      overflow: hidden;
      color: #000;
}

.slick-slide{
      margin: 0 .5rem;
}

.card_header{
      width: 100%;
      height: 15rem;
      flex-direction: column;
      align-items: center;
      clip-path: polygon(0% 0%, 100% 0, 100% 100%, 0 70%);
      background: #aaa;
}

.card_header h2{
      font-size: 4rem;
      padding-top: 3rem;
      color: var(--light);
      font-weight: 700;
}

.card_header span{
      text-transform: uppercase;
      font-family: var(--ft-2);
      font-size: 1.6rem;
      color: var(--light);
      font-weight: 600;
}

.card_header.pink{
      background: var(--grad-pink);
}

.card_header.yellow{
      background: var(--grad-yellow);
}

.card_header.purple{
      background: var(--grad-purple);
}

.card_header.blue{
      background: var(--grad-blue);
}

.card_body{
      flex-direction: column;
      align-items: center;
      width: 100%;
      padding: 0 2.5rem;
}

.card_body__title h2{
      font-size: 3rem;
      color: var(--text-200);
}

.card_body__list{
      margin-top: 2rem;
      width: 100%;
}

.card_body__list ul{
      border-top: solid .1rem var(--border);
}

.card_body__list ul li{
      line-height: 4.2rem;
      border-bottom: solid .1rem var(--border);
      width: 100%;
      text-align: center;
      font-size: 2rem;
      color: var(--text-100);
}

.slick-arrow{
      display: none;
}

.slick-dots{
      bottom: -5rem;
}

.slick-dots li button{
      width: 2.5rem; 
      height: .8rem;
      background: var(--blue-400);
      border-radius: 999px;
}

.slick-dots li.slick-active button{
      background: var(--blue-100);
}

.slick-dots li button:before,
.slick-dots li button:after{
      visibility: hidden;
}

.description_page{
      margin-top: 10rem;
}

.description_page p{
      font-size: 1.8rem;
}