.online_content{
      width: fit-content;
      height: 2.6rem;
      background: rgba(0,0 ,0,.5);
      align-items: center;
      gap: .5rem;
      padding: 0 .6rem;
      border-radius: 999px;
}

.dot{
      width: 1.4rem;
      height: 1.4rem;
      border-radius: 50%;
}

.dot.green{
      background: var(--green);
}

.dot.red{
      background: var(--red);
}

.online_content > span{
      font-family: var(--ft-2);
      font-size: 1.4rem;
      white-space: nowrap;
      padding-right: 1rem;
}