
.home_content{
      width: 100%;
      height: 100%;
      position: relative;
      background: var(--blue-400);
}

/***** BANNER ****/

.banner{
      width: 50%;
      height: 100%;
      position: relative;
}

.home_content.hide{
      animation: hide .3s cubic-bezier(0.7, 0, 0.84, 0) forwards;
}

@keyframes hide{
      0%{
            opacity: 1;
            visibility: visible;
            transform: scale(1);
            filter: blur(0);
      }
      100%{
            opacity: 0;
            visibility: hidden;
            transform: scale(1.6);
            filter: blur(10px);
      }
}

.banner_content{
      width: 100%;
      height: 100%;
      position: relative;
      align-items: center;
      justify-content: center;
      overflow: hidden;
}

.banner_content:before{
      width: 100%;
      height: 100%;
      top: 0;
      left: 0;
      background: rgba(0,0,0,.7);
}

.banner_content img{
      width: auto;
      min-height: 100%;
      height: auto;
      min-height: 100%;
}

.banner_content__text{
      width: 100%;
      height: 100%;
      flex-direction: column;
      align-items: center;
      justify-content: flex-end;
      position: absolute;
      top: 0;
      left: 0;
      padding-bottom: 20rem;
      z-index: 10;
}

.banner_title{
      width: 44rem;
      height: 12rem;
      align-items: center;
      justify-content: center;
      position: relative;
}

.cornner{
      width: 4rem;
      height: 4rem;
      position: absolute;
}

.cornner:nth-of-type(1){
      border-top: solid .2rem var(--blue-200);
      border-left: solid .2rem var(--blue-200);
      top: 0;
      left: 0;
}

.cornner:nth-of-type(2){
      border-top: solid .2rem var(--blue-200);
      border-right: solid .2rem var(--blue-200);
      top: 0;
      right: 0;
}

.cornner:nth-of-type(3){
      border-bottom: solid .2rem var(--blue-200);
      border-left: solid .2rem var(--blue-200);
      bottom: 0;
      left: 0;
}

.cornner:nth-of-type(4){
      border-bottom: solid .2rem var(--blue-200);
      border-right: solid .2rem var(--blue-200);
      bottom: 0;
      right: 0;
}

.banner_title h1{
      font-size: 5rem;
      font-weight: 900;
}

.banner_description{
      margin-top: 2rem;
}

.banner_description p{
      font-size: 1.6rem;
      font-family: var(--ft-1);
}

.banner_logo__panel{
      margin-top: 3rem;
}

/***** LOGO *****/

.logo_context{
      width: 50%;
      height: 100%;
      flex-direction: column;
      align-items: center;
      justify-content: center;
}

.logo_content__circle{
      width: 40rem;
      height: 40rem;
      border-radius: 50%;
      box-shadow: 0 0 0 .2rem var(--blue-200);
      align-items: center;
      justify-content: center;
      position: relative;
}

.logo_content__circle:before{
      width: 36rem;
      height: 36rem;
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%) rotate(0);
      border: solid .4rem var(--blue-100);
      border-top: solid .4rem transparent;
      border-radius: 50%;
      animation: _spin2 4s 1s linear infinite;
}

.circle_effect{
      width: 38rem;
      height: 38rem;
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%) rotate(0);
      border: solid .2rem var(--orange);
      border-bottom: solid .2rem transparent;
      border-radius: 50%;
      animation: _spin 4s linear infinite;
}

@keyframes _spin{
      100%
      {
            transform: translate(-50%, -50%) rotate(360deg)
      }
}

@keyframes _spin2{
      100%
      {
            transform: translate(-50%, -50%) rotate(-360deg)
      }
}

.logo_content__img{
      width: 36rem;
      height: 36rem;
      border-radius: 50%;
      overflow: hidden;
}

.logo_content__img img{
      width: auto;
      min-width: 100%;
      height: auto;
      min-height: 100%;
}

.btn_default{
      margin-top: 3rem;
      padding: 0 5rem;
      line-height: 5rem;
      border-radius: 999px;
      position: relative;
      background: none;
}

.btn_default:before,
.btn_default:after{
      width: 100%;
      height: 100%;
      background: var(--text-grad);
      border-radius: 999px;
      top: 0;
      left: 0;
}

.btn_default:after{
      filter: blur(20px);
}

.btn_default span{
      position: relative;
      z-index: 2;
      color: var(--light);
      text-transform: uppercase;
      font-family: var(--ft-2);
      font-size: 1.6rem;
}